@import '../variables';
@import '../mixins/respond';

.fieldset {

  padding-bottom: 0;
  border-bottom: 1px dotted @color-blue;
  margin-bottom: 40px;

  &:last-of-type {
    margin-bottom: 50px;
  }
}

.fieldset__content {
  padding-bottom: 15px;
  margin-top: 40px;

  .respond(medium, {
    padding-bottom: 10px;
  });
}

.fieldset__button {
  &:extend(.clearfix all);
}

.fieldset__button__button {
  float: right;
}

